*, ::after, ::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

//html
// font-size: 62.5%

body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: #777777;
  font-size: 1.6rem;
  letter-spacing: 0.2rem;
  padding: 5rem;
  background-color: white;

  .container {
    background-color: #F7F7F7; }

  .header {
    position: relative;
    height: 95vh;
    // запись в одну строку
    background: linear-gradient(90.05deg, rgba(186, 133, 84, 0.7) 0.03%, rgba(1, 1, 1, 0.7) 99.94%), url("../image/header-background.jpg") center / cover no-repeat;
    //background-repeat: no-repeat
    //background-size: cover
    //background-position: center
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);

    &__logo {
      position: absolute;
      left: 5rem;
      top: 5rem;
      height: 7rem;
      width: 5.6rem; }

    &__box {
      position: absolute;
      width: 100%;
      text-align: center;
      //выравнивание по центру с помощью абсолютного позиционирования
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      .title-first {
        color: #FFFFFF;

        &__main {
          text-transform: uppercase;
          display: block;
          font-weight: 800;
          font-size: 5rem;
          line-height: 6rem;
          letter-spacing: 1rem;
          margin-bottom: 1rem;
          animation-name: leftToRight;
          animation-duration: 2s;
          animation-delay: 1s;
          animation-timing-function: ease;
          //свойство для устранения багов с анимацией
          backface-visibility: hidden; }

        &__secondary {
          display: block;
          font-weight: 300;
          font-size: 4rem;
          line-height: 4.8rem;
          letter-spacing: 0.5rem;
          animation-name: rightToLeft;
          animation-duration: 2s;
          animation-delay: 1s;
          animation-timing-function: ease;
          backface-visibility: hidden;
          margin-bottom: 6.5rem; } }
      .btn {
        &:link, &:visited {
          position: relative;
          text-transform: uppercase;
          text-decoration: none;
          color: #777777;
          padding: 1.5rem 4rem;
          background: white;
          border-radius: 5rem;
          display: inline-block;
          transition: all .3s;
          font-size: 2rem; }
        &:hover {
          transform: translateY(-3px);
          box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.3);
          &::after {
            opacity: 0;
            transform: scaleX(1.5) scaleY(1.7); } }


        &:active {
          transform: translateY(-2px);
          box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.1); }
        &::after {
          content: '';
          background: white;
          width: 100%;
          height: 100%;
          border-radius: 5rem;
          display: inline-block;
          position: absolute;
          top: 0;
          left: 0;
          z-index: -1;
          transition: all .4s; }
        &_animated {
          animation: btnToUp .5s ease-out .7s;
          animation-fill-mode: backwards; } } }



 }    // анимация в css

  @keyframes leftToRight {
    0% {
      opacity: 0;
      transform: translateX(-10rem); }
    80% {
      transform: translateX(1rem); }

    100% {
      opacity: 1;
      transform: translateX(0rem); } }

  @keyframes rightToLeft {
    0% {
      opacity: 0;
      transform: translateX(10rem); }
    80% {
      transform: translateX(-1rem); }

    100% {
      opacity: 1;
      transform: translateX(0rem); } }

  @keyframes btnToUp {
    0% {
      opacity: 0;
      transform: translateY(5rem); }

    100% {
      opacity: 1;
      transform: translateY(0rem); } } }
